import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class NotificationSetupService {

  private notificationSetupMatDrawer = new BehaviorSubject<any>(null);
  notificationsetupdrawercast = this.notificationSetupMatDrawer.asObservable();

  constructor() { }

  getNotificationSetupMatDrawer(){
    return this.notificationSetupMatDrawer.value
  }

  setNotificationSetupMatDrawer(value){
    this.notificationSetupMatDrawer.next(value);
  }

}
