import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { NotificationSetupService } from './notification-setup.service';


@Injectable({
  providedIn: 'root'
})
export class NotificationSetupResolver implements Resolve<boolean> {

  constructor(
    private router: Router,
    private notificationSetupService:NotificationSetupService
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
  {

    var data = this.notificationSetupService.getNotificationSetupMatDrawer();

    if(data == null){

      const parentUrl = state.url.split('/').slice(0, -3).join('/');

      console.log(parentUrl)

      // Navigate to parent url
      this.router.navigateByUrl(parentUrl);

    }


    return;

  }

}
