import { Component, OnInit, ViewEncapsulation, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Subject } from 'rxjs';

import { MatDrawerToggleResult } from '@angular/material/sidenav';
import { NotificationSetupComponent } from 'src/app/modules/admin/notification/notification-setup/notification-setup.component';

import { NotificationSetupService } from 'src/app/modules/admin/notification/notification-setup/notification-setup.service';
import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-add-notification-category',
  templateUrl: './add-notification-category.component.html',
  styleUrls: ['./add-notification-category.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class AddNotificationCategoryComponent implements OnInit {

  isCategorySubmitted:boolean = true;
  isEdit:boolean = false;

  category:any;

  private _unsubscribeAll: Subject<any>;

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private _changeDetectorRef: ChangeDetectorRef,
    private _notificationSetupComponent: NotificationSetupComponent,
    private notificationSetupService:NotificationSetupService,
    private sharedService:SharedService
  ) {

     // Set the private defaults
     this._unsubscribeAll = new Subject();

   }

   closeDrawer(): Promise<MatDrawerToggleResult>{
    return this._notificationSetupComponent.matDrawer.close();
   }

  goBack() {
    this._notificationSetupComponent.matDrawer.close();
    this._router.navigate(['.'], {relativeTo: this._activatedRoute.parent});
  }

  submitAddEnquiryTypeForm(form) {

  }

  ngOnInit(): void {

    this.category = {

    }

    //Open the drawer
    this.notificationSetupService.notificationsetupdrawercast.subscribe(res => {

      if(res != null) {
        if(res.id != undefined && res.id == this._activatedRoute.params['value'].id){
          this._notificationSetupComponent.matDrawer.open();
          if(this._activatedRoute.params['value'].type == 'edit'){
            this.isEdit = true
            this.category = res.data
          }
          else {
            this.isEdit = false
            this.category = {}
          }
        }
      }

      //Mark for check
      this._changeDetectorRef.markForCheck();

    })

    if(this._activatedRoute.params['value'].type != 'new') {

      this.isCategorySubmitted = true;
      this.isEdit = true;

    }

  }

}
