import { Component, OnInit, ViewEncapsulation, ViewChild, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { Subject } from 'rxjs';
import { MatDrawer } from '@angular/material/sidenav';

import { NotificationSetupService } from './notification-setup.service';
import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'notification-setup',
  templateUrl: './notification-setup.component.html',
  styleUrls: ['./notification-setup.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class NotificationSetupComponent implements OnInit {


  isDataLoaded:boolean = true;

  @ViewChild('matDrawer', {static: true})
  matDrawer: MatDrawer;

  drawerMode: 'over';
  private _unsubscribeAll: Subject<any>;

  constructor(
    private _changeDetectorRef: ChangeDetectorRef,
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private sharedService:SharedService,
    private notificationSetupService: NotificationSetupService
  ) {

    this._unsubscribeAll = new Subject();

  }

  onBackdropClicked() {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    // Go to the parent route
    this._router.navigate(['/notification/setup'], {relativeTo: route});
    this.matDrawer.close();

    // Mark for check
    this._changeDetectorRef.markForCheck();

  }

  addNotificationCategory(event) {

    if(event) {

      // Get the current activated route
      let route = this._activatedRoute;
      while ( route.firstChild )
      {
          route = route.firstChild;
      }

      var id = this.sharedService.guid();

      this.notificationSetupService.setNotificationSetupMatDrawer({id:id, data: null, isedit: false});
      this._router.navigate(['category/new', id], {relativeTo: route});

      //Mark for check
      this._changeDetectorRef.markForCheck();

    }

  }

  editNotificationCategory(item) {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    this.notificationSetupService.setNotificationSetupMatDrawer({id:item.id, data: item, isedit: true});
    this._router.navigate(['category/edit', item.id], {relativeTo: route});

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  viewNotificationCategory(item) {

    // Get the current activated route
    let route = this._activatedRoute;

    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    this.notificationSetupService.setNotificationSetupMatDrawer({id:item.id, data: item, isedit: false});
    this._router.navigate(['category-view', item.id], {relativeTo: route});

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  onGlSearchFilter(event) {

  }

  ngOnInit(): void {
  }

}
