import { Component, OnInit, Output, EventEmitter, ViewEncapsulation } from '@angular/core';

import { Observable, BehaviorSubject, Subscription, Subject } from 'rxjs';
import { filter } from 'rxjs/operators';
import { FormControl } from '@angular/forms';

import { ModalsService } from 'src/app/modules/ui/modals/modals.service';


@Component({
  selector: 'notification-category',
  templateUrl: './notification-category.component.html',
  styleUrls: ['./notification-category.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class NotificationCategoryComponent implements OnInit {

  isExpanded:boolean = false;

  // Private
  private _unsubscribeAll: Subject<any>;

  isDataLoaded:boolean = true;

  _categoryList:any;
  fullCategoryList:any = [];

  searchData:FormControl;

  search:string = '';
  deleteSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 5;

  allData: any;

  @Output() newNotificationCategoryEvent = new EventEmitter<boolean>();
  @Output() editNotificationCategoryEvent = new EventEmitter<any>();
  @Output() viewNotificationCategoryEvent = new EventEmitter<any>();


  constructor(
    private modalsService: ModalsService
  ) {

    // Set the private defaults
    this._unsubscribeAll = new Subject();

    this._categoryList = new BehaviorSubject(null);
    this.searchData = new FormControl('');

   }

  get category$(): Observable<any>{
    return this._categoryList.asObservable();
  }


  addNotificationCategory(event) {
    event.stopPropagation();
    this.newNotificationCategoryEvent.next(true)
  }

  editNotificationCategory(item) {
    this.editNotificationCategoryEvent.next(item)
  }

  viewNotificationCategory(item) {
    this.viewNotificationCategoryEvent.next(item)
  }

  deleteNotificationCategory(item) {
    this.modalsService.showDeleteConfirmModal(item.id)
  }

  ngOnInit(): void {

    // Load types
    let res = [
      {
        "id": 107,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "General",
        "description": "some general related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      },
      {
        "id": 108,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "Time Change",
        "description": "some time change related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      }

    ];

    this.fullCategoryList = res;
    this.allData = res;
    this._categoryList.next(res);

    this.category$.pipe( filter(res => res != null),
    ).subscribe((res:any) => {

      this.totalItems = res.length;
      this.ItemStartIndex = 0;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

    });

    this.deleteSubscribe = this.modalsService.deleteindexcast.subscribe((id:any) => {


      if(id != null) {

        this.fullCategoryList = this.fullCategoryList.filter(item => {
          return item.id != id
        })
        this._categoryList.next(this.fullCategoryList);

      }

    })

  }

  ngOnDestroy(): void
    {
        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
        this.deleteSubscribe.unsubscribe();
    }

}
