import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Subject } from 'rxjs';

import { NotificationSetupComponent } from 'src/app/modules/admin/notification/notification-setup/notification-setup.component';

import { NotificationSetupService } from 'src/app/modules/admin/notification/notification-setup/notification-setup.service';
import { SharedService } from 'src/app/shared/services/shared.service';

import * as _ from 'underscore';
import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-view-notification-category',
  templateUrl: './view-notification-category.component.html',
  styleUrls: ['./view-notification-category.component.scss']
})
export class ViewNotificationCategoryComponent implements OnInit {

  isCategorySubmitted:boolean = true;

  category:any;


  constructor(
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private _changeDetectorRef: ChangeDetectorRef,
    private _notificationSetupComponent: NotificationSetupComponent,
    private notificationSetupService: NotificationSetupService,
    private sharedService:SharedService
  ) {


  }

  goBack() {
    this._notificationSetupComponent.matDrawer.close();
    this.notificationSetupService.setNotificationSetupMatDrawer(null)
    this._router.navigate(['.'], {relativeTo: this._activatedRoute.parent});
  }



  ngOnInit(): void {

    this.category = {

    }

    //Open the drawer
    this.notificationSetupService.notificationsetupdrawercast.subscribe(res => {

      if(res != null) {
        this._notificationSetupComponent.matDrawer.open();
        this.category = res.data
      }

      //Mark for check
      this._changeDetectorRef.markForCheck();

    })

  }

}
