import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { NotificationSetupComponent } from './notification-setup.component';
import { AddNotificationCategoryComponent } from './notification-category/add-notification-category/add-notification-category.component';
import { ViewNotificationCategoryComponent } from './notification-category/view-notification-category/view-notification-category.component';

import { NotificationSetupResolver } from './notification-setup.resolver';

const routes: Routes = [
  { path: '', component: NotificationSetupComponent,
    children: [
      {
        path         : 'category/:type/:id',
        component    : AddNotificationCategoryComponent,
        resolve: {
          NotificationSetupResolver
        }
      },
      {
        path: "category-view/:id",
        component: ViewNotificationCategoryComponent,
        resolve: {
          NotificationSetupResolver
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class NotificationSetupRoutingModule { }

